<template>
    <div style="background-color:#fff;">
        <p class="flex-vertical" style="height:60px;padding:0 16px;margin-bottom:16px;font-size:18px;">修改个人信息</p>
        <van-form @submit="_submit_edit">
            <van-field label="姓名" v-model="userInfoForm.userName" disabled />
            <van-field label="账号" v-model="userInfoForm.viewCode" disabled />
            <van-field label="头像上传">
                <template #input>
                    <van-uploader v-model="headerFileList" max-count="1" :after-read="_read_file_finised" />
                </template>
            </van-field>
            <van-field
                v-model="userInfoForm.userMobile"
                label="新手机号"
                placeholder="新手机号"
                :rules="[{ required: true, pattern: phoneNumberPattern, message: '手机号格式不正确' }]"
            />
            <van-field
                v-model="userInfoForm.viewPsw"
                type="viewPsw"
                label="新密码"
                placeholder="新密码"
                :rules="[{ required: true, message: '请填写新密码' }]"
            />
            <van-field
                v-model="userInfoForm.userIdcard"
                center
                clearable
                label="短信验证码"
                placeholder="短信验证码"
                :rules="[{ required: true, message: '请填写短信验证码' }]"
            >
                <template #button>
                    <van-button
                        size="small"
                        :disabled="!canSendSms"
                        @click="_send_sms"
                        native-type="button"
                    >{{sendSmsBtnText}}</van-button>
                </template>
            </van-field>
            <div style="margin: 16px;">
                <van-button round block type="info" native-type="submit">提交</van-button>
            </div>
        </van-form>
    </div>
</template>

<script>
import config from '@/utils/config';
import { getPersonalInfo, editPersonalInfo, uploadPic, sendSms } from '@/api';
export default {
    data:()=>({
        headerFileList: [{url:''}], // 这个给uploader的v-model使用
        sendingSms: false,
        waitSmsTime: 60,
        countTimer: null,
        phoneNumberPattern: /^1[3456789]\d{9}$/,
        sendSmsBtnText: '发送验证码',
        userInfoForm: {
            userHeader: '',
            userMobile: '',
            viewPsw: '',
            userName: '',
            userIdcard: '',

            viewCode: ''
        }
    }),
    computed: {
        canSendSms: function(){
            if( !this.sendingSms && this.phoneNumberPattern.test(this.userInfoForm.userMobile) )
                return true;
            return false;
        }
    },
    mounted(){
        this.getPersonalInfo();
    },
    destroyed(){
        clearInterval(this.countTimer);
    },
    methods: {
        getPersonalInfo(){
            getPersonalInfo().then(res=>{
                if(res.code === 0){
                    this.headerFileList = [{
                        url: config.host + res.data.userHeader
                    }]
                    this.userInfoForm.userMobile = res.data.userMobile;
                    this.userInfoForm.viewPsw = res.data.viewPsw;
                    this.userInfoForm.userName = res.data.userName;
                    this.userInfoForm.viewCode = res.data.viewCode;

                }else{
                    this.$toast.fail(res.msg);
                }
            })
        },
        _read_file_finised(e){
            let formData = new FormData();
            let config = {
                headers: { "Content-Type": "multipart/form-data" },
            };
            formData.append("file", e.file);
            formData.append("path", 1);
            uploadPic(formData, config).then(res => {
                if (res.code == 0) {
                    this.userInfoForm.userHeader = res.data.filename;
                } else {
                    this.$toast.fail("图片上传失败！");
                }
            });
        },
        _send_sms(){
            let { userMobile, userName } = this.userInfoForm;
            sendSms({
                phoneNumber: userMobile,
                username: userName
            }).then(res=>{
                if(res.code === 0){
                    this.$toast.success('发送验证码成功！');
                    this.countTimer = setInterval(()=>{
                        if(this.waitSmsTime === 0){
                            clearInterval(this.countTimer);
                            this.sendingSms = false;
                            this.sendSmsBtnText = '发送验证码';
                            this.countTimer = 60;
                        }else{
                            this.sendingSms = true; 
                            this.sendSmsBtnText = --this.waitSmsTime + '秒后可再发送';
                        }
                    },1000)
                }else{
                    this.$toast.fail(res.msg);
                }
            })
        },
        _submit_edit(){

            let { userHeader, userMobile, viewPsw, userName, userIdcard } = this.userInfoForm;

            editPersonalInfo({
                userHeader,
                userMobile,
                viewPsw,
                userName,
                userIdcard
            }).then(res=>{
                if(res.code === 0){
                    this.$toast.success('修改信息成功！');
                    this.getPersonalInfo();
                }else{
                    this.$toast.fail(res.msg);
                }
            })
        }
    }
}
</script>

